low;\n      color: $c-brown;\n      padding: 7px 15px;\n      border: 2px solid $c-brown;\n      border-radius: 10px;\n      display: inline-block;\n      font-size: 30px;\n      min-width: 250px;\n    }\n  }\n\n  .bees-inlay {\n    display: flex;\n    flex: 1;\n    justify-content: space-between;\n    align-items: flex-end;\n    padding-bottom: 120px;\n\n    .bee-element {\n      max-width: 200px;\n      display: flex;\n      align-items: center;\n      flex-direction: column;\n\n      @include media-breakpoint-down(sm){\n        max-width: 25%;\n      }\n\n      &#workers-element {\n        .bee-element__image {\n          transform: rotate(-10deg);\n        }\n      }\n\n      &#queen-element {\n        .bee-element__image {\n          transform: rotate(5deg);\n          position: relative;\n\n          &::before {\n            content: '';\n            position: absolute;\n            background-image: url('../../src/images/crown.png');\n            background-size: cover;\n            width: 70px;\n            height: 43px;\n            top: 42px;\n            right: 8px;\n            transform: rotate(22deg);\n\n            @include media-breakpoint-down(sm){\n              width: 40px;\n              height: 28px;\n              top: 27px;\n              right: 8px;\n            }\n\n            @include media-breakpoint-down(xs){\n              width: 28px;\n              height: 19px;\n              top: 15px;\n              right: 0px;\n            }\n          }\n        }\n      }\n\n      &#drones-element {\n        .bee-element__image {\n          transform: rotate(-5deg);\n        }\n      }\n\n      &__health {\n        background: $c-white;\n        color: $c-brown;\n        padding: 7px 15px;\n        border: 2px solid $c-brown;\n        border-radius: 12px;\n        margin-bottom: 20px;\n\n        @include media-breakpoint-down(sm){\n          font-size: 14px;\n        }\n      }\n\n      &__type {\n        margin-top: 20px;\n        background: $c-yellow;\n        color: $c-brown;\n        padding: 7px 15px;\n        border: 2px solid $c-brown;\n        border-radius: 12px;\n        display: inline-block;\n\n        @include media-breakpoint-down(sm){\n          font-size: 14px;\n        }\n      }\n\n      &__number {\n        margin-top: 10px;\n        background: $c-white;\n        color: $c-brown;\n        padding: 7px 15px;\n        border: 2px solid $c-brown;\n        border-radius: 12px;\n\n        @include media-breakpoint-down(sm){\n          font-size: 10px;\n        }\n      }\n\n      img {\n        max-width: 100%;\n      }\n    }\n  }\n}\n\n.shake-element {\n  animation: shake 0.3s forwards;\n}\n\n@keyframes shake {\n  10%,\n  90% {\n    transform: translate3d(-1px, 0, 0);\n  }\n\n  20%,\n  80% {\n    transform: translate3d(2px, 0, 0);\n    filter: brightness(200%);\n  }\n\n  30%,\n  50%,\n  70% {\n    transform: translate3d(-4px, 0, 0);\n    filter: brightness(200%);\n  }\n\n  40%,\n  60% {\n    transform: translate3d(4px, 0, 0);\n    filter: brightness(200%);\n  }\n}","// Media queries breakpoints\n// -------------------------\n// Breakpoints are defined as a map of (name: minimum width), order from small to large\n$grid-breakpoints: (\n  //xs: 0,\t\t\t\t\t// PHONE PORTRAIT\t\t0\t- 543\n  //sm: em(544px, 16px),\t// PHONE LANDSCAPE\t\t544\t- 767\n  //md: em(768px, 16px),\t// TABLET PORTRAIT\t\t768\t- 991\n  //lg: em(992px, 16px),\t// TABLET LANDSCAPE\t\t992\t- 1199\n  //xl: em(1200px, 16px),\t// DESKTOP\t\t\t\t1200 ~\n        xs: 0, // PHONE PORTRAIT\t\t0\t- 543\n        sm: 544px, // PHONE LANDSCAPE\t\t544\t- 767\n        md: 768px, // TABLET PORTRAIT\t\t768\t- 991\n        lg: 992px, // TABLET LANDSCAPE\t\t992\t- 1199\n        xl: 1200px // DESKTOP\t\t\t\t1200 ~\n);\n\n// Colors\n$c-white: #ffffff;\n$c-brown: #984A21;\n$c-yellow: #F4CD68;","// Breakpoint viewport sizes and media queries.\n// --------------------------------------------------\n// - media-breakpoint-up\n// - media-breakpoint-down\n// - media-breakpoint-only\n// --------------------------------------------------\n\n// The breakpoints map defined in the `$grid-breakpoints` global variable is used as the `$breakpoints` argument by default.\n\n// Name of the next breakpoint, or null for the last breakpoint.\n// -------------------------\n//    >> breakpoint-next(sm)\n//    md\n//    >> breakpoint-next(sm, $breakpoints: (xs: 0, sm: 34rem, md: 45rem))\n//    md\n//    >> breakpoint-next(sm, $breakpoint-names: (xs sm md))\n//    md\n@function breakpoint-next($name, $breakpoints: $grid-breakpoints, $breakpoint-names: map-keys($breakpoints)) {\n  $n: index($breakpoint-names, $name);\n  @return if($n < length($breakpoint-names), nth($breakpoint-names, $n + 1), null);\n}\n\n// Minimum breakpoint width. Null for the smallest (first) breakpoint.\n// -------------------------\n//    >> breakpoint-min(sm, (xs: 0, sm: 34rem, md: 45rem))\n//    34rem\n@function breakpoint-min($name, $breakpoints: $grid-breakpoints) {\n  $min: map-get($breakpoints, $name);\n  @return if($min != 0, $min, null);\n}\n\n// Maximum breakpoint width. Null for the largest (last) breakpoint.\n// The maximum value is calculated as the minimum of the next one less 0.1.\n// -------------------------\n//    >> breakpoint-max(sm, (xs: 0, sm: 34rem, md: 45rem))\n//    44.9rem\n@function breakpoint-max($name, $breakpoints: $grid-breakpoints) {\n  $next: breakpoint-next($name, $breakpoints);\n  $value: breakpoint-min($next, $breakpoints);\n\n  @if unit($value) == px {\n    @return if($next, $value - 1px, null);\n  }\n  @else if unit($value) == em {\n    @return if($next, em(px($value) - 1px), null);\n  }\n  @else {\n    @return if($next, $value - 0.001, null);\n  }\n}\n\n// Media of at least the minimum breakpoint width. No query for the smallest breakpoint.\n// Makes the @content apply to the given breakpoint and wider.\n// -------------------------\n@mixin media-breakpoint-up($name, $breakpoints: $grid-breakpoints) {\n  $min: breakpoint-min($name, $breakpoints);\n  @if $min {\n    @media (min-width: $min) {\n      @content;\n    }\n  } @else {\n    @content;\n  }\n}\n\n// Media of at most the maximum breakpoint width. No query for the largest breakpoint.\n// Makes the @content apply to the given breakpoint and narrower.\n// -------------------------\n@mixin media-breakpoint-down($name, $breakpoints: $grid-breakpoints) {\n  $max: breakpoint-max($name, $breakpoints);\n  @if $max {\n    @media (max-width: $max) {\n      @content;\n    }\n  } @else {\n    @content;\n  }\n}\n\n// Media between the breakpoint's minimum and maximum widths.\n// No minimum for the smallest breakpoint, and no maximum for the largest one.\n// Makes the @content apply only to the given breakpoint, not viewports any wider or narrower.\n// -------------------------\n@mixin media-breakpoint-only($name, $breakpoints: $grid-breakpoints) {\n  @include media-breakpoint-up($name, $breakpoints) {\n    @include media-breakpoint-down($name, $breakpoints) {\n      @content;\n    }\n  }\n}"]}]);
